/**
 * 
 * 1attributes
 *  dom  setAttribute()
 * 
 * 
 * 2props
 *  attributes  dom  element[attr] = value
 * 
 * 
 * 3class
 * 
 *  sel 
 * 
 * 4dataset
 *  data-* 
 * 
 * 5eventlisteners
 * 
 * 
 * 6style
 * 
 * delayed/remove/destroy
 * 
 * 
 * 
 * 
 * 
 * init() 
 *  h()  VNode  
 */

 import { h, init } from 'snabbdom'
 // 1
 import style from 'snabbdom/modules/style'
 import eventlisteners from 'snabbdom/modules/eventlisteners'
 // 2
 let patch = init([
    style,
    eventlisteners
 ])
 // 3 h()  
 let vnode = h('div#container', {
    style: {
        backgroundColor: 'pink',
        padding: '30px',
        width: '360px',
        margin: '30px auto'
    }
 }, [
    h('h1', {
      style: {
         fontSize: '26px',
         color: '#333',
         marginBottom: '30px'
      }
    }, ' DOM'),
    h('div.operateBtn', {
      style: {
         display: 'flex',
         justifyContent: 'space-between'
      }
    }, [
      h('div.add', {
         style: {
            width: '50px',
            height: '30px',
            lineHeight: '28px',
            borderRadius: '10px',
            border: '1px solid purple',
            marginBottom: '15px',
            color: 'purple',
            fontSize: '18px',
            textAlign: 'center'
         },
         on: {
            click: addHandler
         }
      }, ''),
      h('div.clear', {
         style: {
            width: '50px',
            height: '30px',
            lineHeight: '28px',
            borderRadius: '10px',
            border: '1px solid #ff5722',
            marginBottom: '15px',
            color: '#ff5722',
            fontSize: '18px',
            textAlign: 'center'
         },
         on: {
            click: clear
         }
      }, '')
    ]),
    h('ul.dataList', [
      h('li', {
         style: {
            border: '1px solid #fff',
            fontSize: '20px',
            color: '#fff',
            lineHeight: '36px',
            marginBottom: '10px',
            display: 'flex',
            justifyContent: 'space-between',
            padding: '0 10px'
         }
      }, [
         h('div.itemName', 'title: '),
         h('div.tag', {
            style: {
               fontSize: '16px'
            }
         }, '')
      ]),
      h('li', {
         style: {
            border: '1px solid #fff',
            fontSize: '20px',
            color: '#fff',
            lineHeight: '36px',
            marginBottom: '10px',
            display: 'flex',
            justifyContent: 'space-between',
            padding: '0 10px'
         }
      }, [
         h('div.itemName', 'title: '),
         h('div.tag', {
            style: {
               fontSize: '16px'
            }
         }, '')
      ])
    ])
 ])
 let app = document.querySelector('#app')

 let oldVnode = patch(app, vnode)

 function addHandler () {
    console.log('')
    vnode = h('div#container', {
      style: {
          backgroundColor: 'pink',
          padding: '30px',
          width: '360px',
          margin: '30px auto'
      }
   }, [
      h('h1', {
        style: {
           fontSize: '26px',
           color: '#333',
           marginBottom: '30px'
        }
      }, ' DOM'),
      h('div.operateBtn', {
         style: {
            display: 'flex',
            justifyContent: 'space-between'
         }
      }, [
        h('div.add', {
           style: {
              width: '50px',
              height: '30px',
              lineHeight: '28px',
              borderRadius: '10px',
              border: '1px solid purple',
              marginBottom: '15px',
              color: 'purple',
              fontSize: '18px',
              textAlign: 'center'
           },
           on: {
              click: addHandler
           }
        }, ''),
        h('div.clear', {
            style: {
               width: '50px',
               height: '30px',
               lineHeight: '28px',
               borderRadius: '10px',
               border: '1px solid #ff5722',
               marginBottom: '15px',
               color: '#ff5722',
               fontSize: '18px',
               textAlign: 'center'
            },
            on: {
               click: clear
            }
         }, '')
      ]),
      h('ul.dataList', [
        h('li', {
           style: {
              border: '1px solid #fff',
              fontSize: '20px',
              color: '#fff',
              lineHeight: '36px',
              marginBottom: '10px',
              display: 'flex',
              justifyContent: 'space-between',
              padding: '0 10px'
           }
        }, [
           h('div.itemName', ': '),
           h('div.tag', {
               style: {
                  fontSize: '16px'
               }
            }, '')
        ]),
        h('li', {
           style: {
              border: '1px solid #fff',
              fontSize: '20px',
              color: '#fff',
              lineHeight: '36px',
              marginBottom: '10px',
              display: 'flex',
              justifyContent: 'space-between',
              padding: '0 10px'
           }
        }, [
           h('div.itemName', ': ~'),
           h('div.tag', {
               style: {
                  fontSize: '16px'
               }
            }, '')
        ]),
        h('li', {
            style: {
               border: '1px solid #fff',
               fontSize: '20px',
               color: '#fff',
               lineHeight: '36px',
               marginBottom: '10px',
               display: 'flex',
               justifyContent: 'space-between',
               padding: '0 10px'
            },
         }, [
            h('div.itemName', ': '),
            h('div.tag', {
               style: {
                  fontSize: '16px'
               }
            }, '')
         ])
      ])
   ])
   patch(oldVnode, vnode)
 }

 function clear () {
   patch(vnode, h('!'))
 }

 

